  
footer {
    background-color: black;
    height: 180px;
    padding: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

footer ul li a,
footer ul li {
    text-decoration: none;
    color: #b8b8b8;
    font-size: 12px;
}

footer ul li {
    list-style: none;
    text-transform: uppercase;
    margin-bottom: 5px;
    cursor: pointer;
}

footer ul li h3 {
    text-decoration: none;
    color: #dcdcdc;
    font-size: 16px;
    margin-bottom: 10px;
    cursor: pointer;
}

.link-item:hover {
    text-decoration: none;
    color: #06b4a3 !important
}